.preload-wrap
{

    position: fixed;
    z-index: 10000;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: $primary-bg;
    transition: all 1s ease-out;
}
.preload-wrap
{
    svg
    {
        max-width: 90%;
        max-height: 90%;
    }
}
.preload-wrap.hidden
{
    transform: translateY(-100%);
}

@media (max-width: 991px) {
    .preload-wrap
    {
        display: flex;
    }

}