.flats-filter__head
{
    margin-bottom: 5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-chose
    {
        display: flex;
        column-gap: 4.6rem;
    }
}

.flats-filter__visual
{
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    column-gap: 2.8rem;
}

.flats-filter__form
{
    display: flex;
    flex-direction: column;
    row-gap: 2.8rem;
    &-label
    {
        margin-bottom: 3rem;
        color: #F5F5F5;
        font-size: 2rem;
        font-weight: 500;
        line-height: 1.45em;
    }
    &-options
    {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        column-gap: 2rem;
    }
    &-option
    {
        position: relative;
        input
        {
            display: none;
        }
        input:checked + label
        {
            color: #85919D;
            background-color: #F5F5F5;
        }
        label
        {
            width: 5rem;
            height: 5rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #F5F5F5;
            color: #F5F5F5;
            font-size: 2rem;
            font-weight: 700;
            line-height: 1.4em;
            cursor: pointer;
        }
    }
}
